import React from "react";
import "./ban-nhan-su.css";
import { useCMS } from "../../contexts/CMSContext";

const BanNhanSu: React.FC = () => {
  const { displayData } = useCMS();
  const content = displayData.pages.page2.banNhanSuContent;

  if (!content) return null;

  return (
    <section className="nhansu">

      {/* ===== DESKTOP VERSION ===== */}
      <div
        className="nhansu-desktop"
        style={{
          backgroundImage: `url(${content.desktopBackground})`
        }}
      >
        <div className="nhansu-desktop-inner">
          {content.groups.map((group, index) => (
            <div
              key={group.id}
              className="nhansu-col"
              data-aos="fade-up"
              data-aos-delay={index * 200}
            >
              <h3 className="nhansu-title">{group.title}</h3>
              <ol>
                {group.members.map((member, i) => (
                  <li key={i}>{member}</li>
                ))}
              </ol>
            </div>
          ))}
        </div>
      </div>

      {/* ===== MOBILE VERSION ===== */}
      <div className="nhansu-mobile">
        {content.groups.map((group, index) => (
          <React.Fragment key={group.id}>
            <div
              className="nhansu-mobile-header"
              style={{
                backgroundImage: `url(${group.mobileBackground})`
              }}
            >
              <h3 className="nhansu-title">{group.title}</h3>
            </div>

            <div
              className="nhansu-mobile-content"
              data-aos="fade-up"
              data-aos-delay={100 + index * 200}
            >
              <ol>
                {group.members.map((member, i) => (
                  <li key={i}>{member}</li>
                ))}
              </ol>
            </div>
          </React.Fragment>
        ))}
      </div>
    </section>
  );
};

export default BanNhanSu;
