import React from "react";
import "./loi-tu.css";
import { useCMS } from "../../contexts/CMSContext";

const LoiTuBach: React.FC = () => {
  const { displayData } = useCMS();
  const content = displayData.pages.page2.loiTuBachContent;

  if (!content) return null;

  const imgOnRight = (content.imagePosition || 'right') === 'right';

  return (
    <section className="loi-wrapper">
      <div className="loi-container">

        {/* IMAGE */}
        <div className="loi-image" style={{ order: imgOnRight ? 2 : 1 }}>
          <img src={content.image.src} alt={content.image.alt} />
        </div>

        {/* TEXT */}
        <div className="loi-text" style={{ order: imgOnRight ? 1 : 2 }}>
          <h1 className="loi-title">{content.title}</h1>

          <p className="loi-greeting">{content.greeting}</p>

          {content.paragraphs.map((text, index) => (
            <p key={index}>{text}</p>
          ))}

          <p className="loi-closing">{content.closing}</p>

          <div className="loi-signature-block">
            <div>{content.signature.date}</div>
            <div>{content.signature.organization}</div>
            <div>{content.signature.role}</div>
            <div className="loi-signature-name">
              {content.signature.name}
            </div>
          </div>
        </div>

      </div>
    </section>
  );
};

export default LoiTuBach;
