import React from "react";
import "./bon-dai-tong-chi.css";
import { useCMS } from "../../contexts/CMSContext";

const BonDaiTongChi: React.FC = () => {
  const { displayData } = useCMS();
  const content = displayData.pages.page2.bonDaiTongChiContent;

  if (!content) return null;

  return (
    <>
      <section
        className="tongchi-section"
        style={{
          backgroundImage: `url('${content.backgroundImage}')`
        }}
      >
        <div className="tongchi-container">
          <h2 className="tongchi-title" data-aos="fade-up">{content.title}</h2>
          <div className="tongchi-underline" />

          <div className="tongchi-content-desktop">
            <p className="tongchi-desc" data-aos="fade-up" data-aos-delay="100">
              {content.subtitle}
            </p>

            <ol className="tongchi-list" data-aos="fade-up" data-aos-delay="200">
              {content.items.map((item) => (
                <li key={item.id}>{item.title}</li>
              ))}
            </ol>
          </div>
        </div>
      </section>

      <section className="tongchi-mobile-content">
        <div className="tongchi-mobile-container">
          <p className="tongchi-desc" data-aos="fade-up">
            {content.subtitle}
          </p>

          <ol className="tongchi-list" data-aos="fade-up" data-aos-delay="100">
            {content.items.map((item) => (
              <li key={item.id}>{item.title}</li>
            ))}
          </ol>
        </div>
      </section>

      {/* Mobile background handling via style injection since widely supported media query in style tag is tricky in inline styles */}
      <style>{`
        @media (max-width: 768px) {
          .tongchi-section {
            background-image: url('${content.mobileBackgroundImage || content.backgroundImage}') !important;
          }
        }
      `}</style>
    </>
  );
};

export default BonDaiTongChi;